import React from "react";
import "./leak_roof.css";

const LeakRoof = ({ onOrderClick }) => {
  return (
    <div className="Leak_roof mrg-top">
      <div className="Leak-roof_special-container roof_special-container">
        <h1 className="Leak_roof-title roof-title">
          Ремонт і усунення протікань
        </h1>
        <p className="Leak-roof_special-text roof_special-text">
          Протікання даху - це не тільки незручність, а й серйозна загроза для
          конструкції будинку. Ми виконуємо повний цикл робіт: від виявлення
          причин течі до відновлення герметичності. Використовуємо професійне
          обладнання для діагностики та якісні гідроізоляційні матеріали, які
          забезпечують довгостроковий захист.
        </p>
        <div className="Leak_roof_bot-div roof_bot-div">
          <h3> Переваги:</h3>
          <ul className="special-text_list">
            <li>Локалізація проблеми без заміни даху.</li>
            <li>
              Використання сучасних матеріалів, які не потребують частого
              оновлення.
            </li>
            <li>
              Усунення проблеми у найкоротший термін, щоб мінімізувати наслідки.
            </li>
          </ul>
        </div>
        <div
          className="Soft-roof_btn-container btn-special_container"
          onClick={onOrderClick}
        >
          <div className="feedback-container">
            <div className="feedback-border">
              <div className="feedback">
                <div className="feedback-text-container">
                  <p className="feedback-text">Замовити дзвінок</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <img
        className="Leak-roof_special-img roof_special-img"
        src="./img/Leak.webp"
        alt=""
      ></img>
    </div>
  );
};

export default LeakRoof;
